@import "../../../styles/variables";

.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  max-width: 283px;

  &__up {
    margin-bottom: 16px;
    position: relative;
  }

  &__image {
    display: block;

    max-width: 100%;
    height: auto;

    border-radius: 16px;
  }

  &__name {
    @include setTextStyle(700, 22px, 120%);
    color: $text-color;

    min-height: 53px;

    margin-bottom: 8px;

    overflow: hidden;
  }

  &__location {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    align-items: center;

    @include setTextStyle(400, 22px, 120%);
    color: $primary-color;
    
    margin-bottom: 8px;
  }

  &__locationIcon {
    margin-right: 8px;
  }

  &__price {
    margin-bottom: 16px;
  }

  &__btn {
    width: 100%;
    height: 60px;

    background: $base-color;
    mix-blend-mode: normal;

    margin-top: auto;

    border: none;

    transition: background $transition-long ease;
  }

  &__btn:hover {
    box-shadow: 0px 5px 10px rgba(253, 114, 92, 0.22),
      0px 20px 40px rgba(250, 99, 35, 0.24);
    background-color: #da3802;
    transition: background $transition-long ease;
  }

  &__btns {
    display: flex;
    justify-content: space-between;

    margin-top: auto;
  }
}

.card__counter {
  width: calc(100% - 140px - 30px);
  padding: 21px 0;
}

@media(max-width:440px) {
  .card__counter {
    width: 100%;
    margin-right: 0;
  }
}